.nav {

  &__list {
    display: flex;
    gap: 48px;
    list-style: none;
  }

  &__item {
    font-family: "Poppins", sans-serif;
    font-size: 13px;
    font-style: normal;
    font-weight: 700;
    line-height: 19.5px;
    letter-spacing: 3px;
    text-transform: uppercase;
  }

  &__link {
    color: #fff;
    text-decoration: none;

    &:hover {
      color: #2060f6;
      transition: color $effectDuration;
    }
  }
}
